*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}

li{
    list-style: none;
}

a{
    text-decoration: none;
}

header{
    background-color: aliceblue;
}

.container{
    width: calc(100% - 10%);
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;  
}

.brand-name{
    font-size: 2.5rem;
    font-weight: bold;
    color: #000;
    padding: 20px 0;
}

.col-1{
    flex-grow: 1;

}

.col-2{
    flex-grow: 2;
}

.col-3{
    flex-grow: 1;

}
.navbar ul{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.navbar ul li{
    /* margin: 0 20px; */
}
.navbar ul li a{
    color: rgb(71, 71, 71);
    cursor: pointer;
}

.nav-icons{
    display: flex;
    align-items: center;  
    justify-content: flex-end;
}

.nav-icons li{
    margin: 0 10px;
    font-size: 20px;
    cursor: pointer;
}

.nav-icons li:last-child{
    display: none;
}

.close{
    display: none;
}

.navbar .nav-icons{
    display: none;
}

@media screen and (max-width: 960px){
    .nav-icons li:last-child{
        display: block;
    }
    
    .col-2{
        flex-grow: unset;
    }

    .navbar{
        position: absolute;
        top: 0;
        right: 0;
        background-color: aliceblue;
        width: 200px;
        padding: 0 50px;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }

    .close{
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    .close i{
        font-size: 30px;
        cursor: pointer;
    }

    .navbar ul:nth-child(2){
        flex-direction: column;
        align-items: flex-start;
    }

    .navbar ul li{
        margin: 10px 0;
        padding: 20px 0;
    }

    .navbar .nav-icons{
        display: flex;
        justify-content: space-evenly;
    }


}

